import { getToken } from './auth';

/**
 * Check if user has tenant access
 * Since backend doesn't have /access endpoint, we validate by:
 * 1. Check if token exists
 * 2. Make a test call to tenant chat endpoint
 * Backend will return 403 if user not authorized
 */
export const checkTenantAccess = async (tenantId: string): Promise<boolean> => {
  console.log('[checkTenantAccess] Called for tenant:', tenantId);
  const token = getToken();
  if (!token) return false;

  try {
    // Test authorization with a lightweight query
    const response = await fetch(`/api/tenant/${tenantId}/chat`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      },
      body: JSON.stringify({ message: '__check_access__' })
    });

    // If we get 200 or 201, user has access
    // If 401/403, user doesn't have access
    return response.ok;
  } catch {
    return false;
  }
};

/**
 * Send message to tenant chat endpoint
 * Automatically includes auth token if available
 */
export const sendTenantMessage = async (tenantId: string, message: string) => {
  console.log('[sendTenantMessage] Called for tenant:', tenantId);
  const token = getToken();
  
  const response = await fetch(`/api/tenant/${tenantId}/chat`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      ...(token && { 'Authorization': `Bearer ${token}` })
    },
    body: JSON.stringify({ message })
  });

  if (!response.ok) {
    throw new Error(`API call failed: ${response.status}`);
  }

  return response.json();
};

/**
 * Send message to customer (public) chat endpoint
 * No authentication required
 */
export const sendCustomerMessage = async (tenantId: string, message: string) => {
  const response = await fetch(`/tenant/${tenantId}/chat`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({ message })
  });

  if (!response.ok) {
    throw new Error(`API call failed: ${response.status}`);
  }

  return response.json();
};